<template>
  <div id="app">
    <mt-header fixed title="nCov-2019 charts"></mt-header>
    <div id="mapdiv" ref="mapContainer"></div>
    <OverAllData />
  </div>
</template>

<script>
import mapboxgl from "mapbox-gl";
import OverAllData from "./components/OverAllData.vue";
export default {
  components: { OverAllData },
  name: "App",
  data() {
    return {
      // date: null,
      // confirmed: null,
      // suspected: null,
      // cured: null,
      // dead: null
    };
  },
  methods: {
    init() {
      //创建地图对象
      mapboxgl.accessToken =
        "pk.eyJ1Ijoiamltd2VpIiwiYSI6ImNqN3ZoczZhZjZiZm0zMm8yeW00b2VkdjkifQ.Q_wogxtnFE11PatzGRkHEA";
      var map = new mapboxgl.Map({
        container: this.$refs.mapContainer,
        style: "mapbox://styles/jimwei/ck4mcmd4l7ufr1cpmh965ht2a",
        zoom: 2,
        center: [106, 36]
      });
      // map.setCenter([114, 32]);
    }
  },
  mounted() {
    this.init();
  }
};
</script>

<style>
body {
  margin: 0;
}
#app {
  padding-top: 40px;
}
#mapdiv {
  width: 100%;
  height: 300px;
  /* background-color: aqua; */
}
</style>



